.playlist-image-section {
    padding: 40px 30px;
    padding-left: 60px;
}

.playlist-container-div {
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0;
}

.playlist-card {
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    width: 100%;
    height: 200px;
    background: linear-gradient(to right, #424a57, #1f2937);
    background: linear-gradient(to right, #162234 40px, #a7bad2);
    /* background: #424a57; */

}

.playlist-image img {
    width: 200px;
    height: 200px;
    object-fit: cover;
}

.playlist-content {
    padding: 15px;
}

.playlist-name {
    font-size: 44px;
    margin: 0;
    color: white;
}

.artist {
    font-size: 26px;
    color: #e6e5e5;
    margin: 5px 0;
}

.total-songs {
    font-size: 16px;
    color: #e6e5e5;
    margin: 0;
}

.playlist-song-section {
    padding: 40px 30px;
    padding-left: 60px;

}

.main-playlist-img-contianer {
    background: linear-gradient(to bottom, #13a476, #1f2937);
    background: linear-gradient(to bottom, #13a476, #1f2937 500px);
}

.playlist-song-contianer {
    font-family: Arial, sans-serif;
    background-color: #3d4c61;
    margin: 0;
    height: 500px;
    overflow-y: scroll;

}

.playlist-table {
    border-collapse: collapse;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}

.playlist-table th {
    padding: 18px 15px;
    text-align: left;
    border-bottom: 1px solid #ccc;
    color: white;
}

.playlist-table td {
    padding: 13px 15px;
    text-align: left;
    border-bottom: 1px solid #aca8a8;
    background-color: #485669;
    color: white;
}

.playlist-table th {
    background-color: #3d4c61;
    color: white;
}

.playlist-table tbody tr:hover {
    background-color: #f9f9f9;
}

.playlist-page {
    width: 100%;
    height: auto;
    display: flex;

}

.playlist-page-left {
    width: 75%;
    height: auto;
}

.playlist-page-right {
    width: 25%;
    height: auto;
    background: linear-gradient(to bottom, #13a476, #1f2937 500px);
}