* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

nav {
    height: 10vh;
    width: 100%;
    background-color: #374558;
}

.navbar {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0px 10px 0px 53px;
    position: relative;
}

.navbar ul {
    display: flex;
    align-items: center;

}

.navbar ul li {
    list-style-type: none;
    width: 93px;
    font-size: 17px;
}

.navbar ul li a {
    text-decoration: none;
    color: white;

}

.menu-logo {
    margin: 0px 8px 0px 2px;

}

.menu-logo i {
    font-size: 15px;
}

.site-name {
    width: 190px;
    color: white;
}

.bot-link {
    position: absolute;
    right: 100px;
}

.bot-link h3 a {
    text-decoration: none;
    color: white;

    border: 1px solid white;
    border-radius: 25px;
    padding: 8px 12px;
    background-color: #139f74;
    /* box-shadow: 0px 0px 2px 2px #c4c3c3; */
}

.bot-link h3 a:hover {
    background-color: #13a476;
    opacity: 0.8;

}

.section-album {
    width: 100%;

}

.home-page-main-section {
    width: 100%;

}

.home-page-sub-section {
    height: 800px;
    display: flex;
    background: linear-gradient(to bottom, #13a476 40px, #1f2937);

}

.home-page-left-section {
    width: 75%;
    height: auto;
    /* background-color: #13a476; */
    /* background-color: #1f2937; */


}

.home-page-right-section {
    width: 25%;
    height: 540px;
    /* background: linear-gradient(to bottom, #13a476 40px, #1f2937); */
}

.search-contianer {
    width: 100%;
    background-color: rgb(62, 77, 97);
    height: 88px;
    /* background: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSVzehHiW8WovHOGvd47ZehD2Q2KTECvSbyEQ&usqp=CAU") no-repeat center center/cover; */
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(62, 77, 97, 0.39);
}

.search-sub-container {
    position: relative;
    display: inline-block;
}

.search-box {
    padding: 8px 30px 8px 10px;
    font-size: 16px;
    border: 2px solid #ccc;
    border-radius: 25px;
    outline: none;
    width: 450px;
}

.search-icon {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    font-size: 18px;
    color: #999;

}

.search-icon:hover {
    color: #333;

}

.album-container {
    padding: 40px 20px 35px;
    background: linear-gradient(to bottom, #13a476 40px, #1f2937);
}

.album-card-container {
    display: flex;
    justify-content: center;
    overflow: hidden;
    height: auto;
    margin-top: 31px;
    flex-wrap: wrap;

}

.album-card {
    background-color: #3d4a5b;
    color: #fff;
    width: 192px;
    border-radius: 15px;
    box-shadow: 0px 0px 7px 2px #2a3441;
    height: auto;
    padding: 15px;
    margin: 10px 15px 31px;
    position: relative;

}

.album-image {
    width: 100%;
    height: 160px;
    display: block;
}

.album-details {
    padding: 20px 5px;
    padding-bottom: 0px;
    margin-bottom: 10px;
}

.album-title {
    font-size: 19px;
    margin: 0;
}

.artist-name {
    font-size: 14px;
    margin: 5px 0 0;
}



.heading-sec {
    color: white;
    margin: 20px 0px 12px 15px;
    font-size: 23px;
}

.div-play-logo {
    position: absolute;
    right: 12px;
    font-size: 42px;
    color: #fff;
    color: #13a476;
    top: 196px;
}


.song-contianer {
    padding: 45px 15px 30px;
    background-color: #222a35;
}

.div-play-logo i {
    font-weight: 900;
    box-shadow: 0px 0px 2px 5px #314055;
    border-radius: 50%;
}

.song-card-container {
    display: flex;
    height: auto;
    justify-content: center;
    margin-top: 31px;
    flex-wrap: wrap;
}

.scroll-page {
    height: 715px;
    overflow-y: scroll;
}

footer {
    margin: 0;
    padding: 0;
}

.footer {
    background-color: #181e27;
    color: #fff;
    text-align: center;
    padding: 20px 0;
}

.footer-line {
    height: 1px;
    background-color: #fff;
    width: 800px;
    margin: 0 auto;
    margin-bottom: 7px;
}